#root {
  background: #f5f5f5;
  min-height: 850px;
  padding-top: 5%;
}

.box {
  background: #fff;
  height: 600px;
  width: 70%;
  -webkit-box-shadow: 0 4px 5px -3px lightgray;
          box-shadow: 0 4px 5px -3px lightgray;
}

.box p {
  font-size: 14px;
  font-family: sans-serif;
  font-weight: 100;
  margin-bottom: 30px;
}

.phone-img {
  height: 400px;
  margin-left: 90px;
}

.info-section {
  padding-top: 30px;
}

.phone-img-section {
  padding-top: 5%;
  padding-left: 5%;
}

h2 {
  font-size: 25px;
  font-family: sans-serif;
  font-weight: 100;
  margin-bottom: 20px;
}

#stars-img {
  width: 80px;
  margin-bottom: 10px;
}

.colour-section span {
  font-weight: bold;
}

.colour-choice {
  height: 40px;
  width: 40px;
  border-radius: 7px;
  border: solid #cccccc 1px;
  margin-top: 0;
}

#gold-choice {
  background: -webkit-gradient(linear, left top, left bottom, from(#f3e1c9), to(#e2b275));
  background: -webkit-linear-gradient(#f3e1c9, #e2b275);
  background: -o-linear-gradient(#f3e1c9, #e2b275);
  background: linear-gradient(#f3e1c9, #e2b275);
  margin: 0 4px 4px 4px;
}

#silver-choice {
  background: -webkit-gradient(linear, left top, left bottom, from(#e6e6e6), to(#8c8c8c));
  background: -webkit-linear-gradient(#e6e6e6, #8c8c8c);
  background: -o-linear-gradient(#e6e6e6, #8c8c8c);
  background: linear-gradient(#e6e6e6, #8c8c8c);
  margin: 0 4px 4px 4px;
}

#grey-choice {
  background: #000000;
  margin: 0 4px 4px 4px;
}

.capacity-choice {
  background: #f2f2f2;
  height: 40px;
  width: 40px;
  margin: 0 5px 5px 5px;
  border-radius: 7px;
  border: solid #cccccc 1px;
  padding-top: 10px;
  padding-left: 10px;
}

.capacity-section span {
  font-weight: bold;
}

.price-section {
  margin-top: 70px;
  padding-top: 27px;
  height: 120px;
  background: #f2f2f2;
}

.price-section p {
  padding-top: 10px;
}

.price-section span {
  font-size: 25px;
  font-weight: bold;
  color: #e60000;
}

.price-section .section1 {
  border-right: solid #cccccc 1px;
}



